import template from './editable-dao-input.html';
import './editable-dao-input.scss';

function EditableDaoInputDirective() {
  'ngInject';

  return {
    restrict: 'E',
    template: template,
    scope: {
      data: '=',
      options: '='
    },
    replace: true,
    controller: editableDaoInputController,
    controllerAs: 'vm'
  };
}
class editableDaoInputController {
  constructor($scope, $element, $timeout) {
    'ngInject';

    this.$scope = $scope;
    this.$el = $element;
    this.$timeout = $timeout;

    this.init();
  }

  init() {
    this.options = this.$scope.options;
    //  watch this.data this.data  this.$scope.data
    this.data = this.$scope.data;
    this.dataOrigin = this.$scope.data;
    this.isEdit = this.options.editState;
    this.isError = false;
    this.isFocus = this.isEdit;
    this.activate();
  }

  activate() {
    this.$scope.$watch(() => this.data, newVal => this.setInputData(newVal));
    this.$scope.$watch(() => this.$scope.data, newVal => this.setOriginData(newVal));
  }

  setInputData(val) {
    //  checkFun  this.$scope.data
    this.$scope.data = val;
    if (val === this.dataOrigin) {
      // 
      this.isError = false;
      return;
    }
    //  checkFun
    // 
    this.$timeout(() => {
      this.isError = !this.options.checkFun();
    });
  }

  setOriginData(val) {
    //  this.data !== this.$scope.data  this.$scope.data 
    //  dataOrigin  watch this.data  this.$scope.data
    if (this.data !== val) {
      this.dataOrigin = val;
      // 
      this.data = val;
    }
  }

  cancel() {
    this.isEdit = false;
    this.isError = false;
    this.data = this.dataOrigin;
  }

  edit() {
    this.isEdit = true;
    //  disabled  $timeout
    this.$timeout(() => {
      angular.element(this.$el[0]).find('input')[0].focus();
    });
  }

  // ng-keyPress  esc  keyUpFirefox  keydown  esc 
  keyUp(e) {
    switch(e.keyCode) {
      case 13: // enter 
        this.save();
        break;
      case 27: // esc 
        this.cancel();
        break;
    }
  }

  save() {
    if (this.options.checkFun()) {
      this.dataOrigin = this.data;
      this.options.successFun();
      this.isEdit = false;
      this.isError = false;
    } else {
      this.isError = true;
      angular.element(this.$el[0]).find('input')[0].focus();
    }
  }
}

export default EditableDaoInputDirective;



